<template>
  <div class="inv">
    <header class="inv__header">
      <h2>Inventario</h2>
      <nav class="tabs" role="tablist" aria-label="Ubicaci√≥n">
        <button
          v-for="u in ubicaciones"
          :key="u"
          class="tab"
          :class="{ 'tab--active': ubicacionActiva === u }"
          role="tab"
          :aria-selected="ubicacionActiva === u"
          @click="setUbicacion(u)"
        >
          {{ u }}
          <span class="tab__count" v-if="conteos[u] !== undefined">{{ conteos[u] }}</span>
        </button>
      </nav>
      
    <!-- üîπ Bot√≥n para ir a ConsultaSolisitudes.vue -->
    <button class="btn btn--accent" @click="irAConsultas">
      Ver solicitudes
    </button>
    </header>

    <!-- FORM: solo descripci√≥n -->
    <section class="card">
      <h3 class="card__title">Nueva solicitud ‚Äî {{ ubicacionActiva }}</h3>
      <form class="form" @submit.prevent="crearItem">
        <div class="form__row">
          <input
            v-model.trim="nuevoItem.descripcion"
            class="input"
            placeholder="Describe lo que necesitas..."
            required
          />
          <button class="btn" type="submit" :disabled="enviando || !nuevoItem.descripcion">
            <span v-if="!enviando">Enviar</span>
            <span v-else class="spinner"></span>
          </button>
        </div>
        <p v-if="error" class="form__error">{{ error }}</p>
      </form>
    </section>

    <!-- LISTADO: sin precio ni ubicaci√≥n, con editar/eliminar -->
    <section class="card">
      <div class="card__title-row">
        <h3 class="card__title">Listado ‚Äî {{ ubicacionActiva }}</h3>
        <!-- total oculto, pero puedes reusarlo si lo necesitas -->
      </div>

      <div v-if="cargando" class="empty">
        <span class="spinner"></span> Cargando‚Ä¶
      </div>

      <ul v-else-if="filtrados.length" class="list">
        <li
          v-for="item in filtrados"
          :key="item.id_item ?? item.id ?? item.descripcion"
          class="list__item"
        >
          <div class="list__main">
            <template v-if="editandoId === (item.id_item ?? item.id)">
              <input
                v-model.trim="editBuffer.descripcion"
                class="input input--inline"
                placeholder="Descripci√≥n"
                required
              />
              <!-- Solo BARRO permite editar estado -->
              <select
                v-if="item.ubicacion === 'Barra'"
                v-model="editBuffer.estado"
                class="input input--inline"
              >
                <option value="Pendiente">Pendiente</option>
                <option value="Entregado">Entregado</option>
              </select>
            </template>
            <template v-else>
              <span class="list__desc">{{ item.descripcion }}</span>
              <span class="chip" :class="item.estado === 'Entregado' ? 'chip--ok' : 'chip--warn'">
                {{ item.estado }}
              </span>
            </template>
          </div>

          <div class="actions">
            <template v-if="editandoId === (item.id_item ?? item.id)">
              <button class="btn btn--ghost" @click="cancelarEdicion">Cancelar</button>
              <button class="btn" :disabled="guardando" @click="guardarEdicion(item)">
                <span v-if="!guardando">Guardar</span>
                <span v-else class="spinner"></span>
              </button>
            </template>
            <template v-else>
              <button class="icon" title="Editar" @click="iniciarEdicion(item)">‚úèÔ∏è</button>
              <button class="icon icon--danger" title="Eliminar" @click="eliminarItem(item)">üóëÔ∏è</button>
            </template>
          </div>
        </li>
      </ul>

      <div v-else class="empty">
        <p>No hay √≠tems en <strong>{{ ubicacionActiva }}</strong>.</p>
        <p class="muted">Agrega el primero con el formulario de arriba.</p>
      </div>
    </section>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
import { useRouter } from 'vue-router'   // ‚¨ÖÔ∏è IMPORTANTE
import axios from 'axios'
const router = useRouter()

const irAConsultas = () => {
  router.push({ name: 'ConsultaSolisitudes' })
}

// Endpoint (seg√∫n tu server)
const API = 'http://localhost:8080/api/inventario-cosina' // montado en server.js 

const ubicaciones = ['Cocina', 'Barra']
const ubicacionActiva = ref('Cocina')

const items = ref([])
const cargando = ref(true)
const enviando = ref(false)
const guardando = ref(false)
const error = ref('')

// crear: solo descripci√≥n + ubicaci√≥n
const nuevoItem = ref({ descripcion: '', ubicacion: ubicacionActiva.value })

// edici√≥n inline
const editandoId = ref(null)
const editBuffer = ref({ descripcion: '', estado: 'Pendiente' })

const obtenerItems = async () => {
  cargando.value = true
  try {
    const { data } = await axios.get(API) // GET / 
    items.value = Array.isArray(data) ? data : []
  } catch (e) {
    console.error(e)
    error.value = 'No se pudieron cargar los √≠tems'
  } finally {
    cargando.value = false
  }
}

const crearItem = async () => {
  error.value = ''
  if (!nuevoItem.value.descripcion) return
  enviando.value = true
  try {
    const payload = {
      descripcion: nuevoItem.value.descripcion,
      ubicacion: ubicacionActiva.value
    }
    const { data } = await axios.post(API, payload) // POST / 
    if (data) items.value.unshift(data)
    nuevoItem.value = { descripcion: '', ubicacion: ubicacionActiva.value }
  } catch (e) {
    console.error(e)
    error.value = 'No se pudo enviar la solicitud'
  } finally {
    enviando.value = false
  }
}

const iniciarEdicion = (item) => {
  editandoId.value = item.id_item ?? item.id
  editBuffer.value = {
    descripcion: item.descripcion,
    estado: item.estado ?? 'Pendiente'
  }
}

const cancelarEdicion = () => {
  editandoId.value = null
  editBuffer.value = { descripcion: '', estado: 'Pendiente' }
}

const guardarEdicion = async (item) => {
  guardando.value = true
  try {
    // Regla: Cocina -> solo puede cambiar descripci√≥n
    //        Barra  -> puede cambiar descripci√≥n y estado
    const body =
      item.ubicacion === 'Cocina'
        ? { descripcion: editBuffer.value.descripcion }
        : { descripcion: editBuffer.value.descripcion, estado: editBuffer.value.estado }

    const id = item.id_item ?? item.id
    const { data } = await axios.put(`${API}/${id}`, body) // PUT /:id 

    // Actualiza en memoria
    const idx = items.value.findIndex((x) => (x.id_item ?? x.id) === id)
    if (idx !== -1) items.value[idx] = { ...items.value[idx], ...data }

    cancelarEdicion()
  } catch (e) {
    console.error(e)
    error.value = 'No se pudo guardar la edici√≥n'
  } finally {
    guardando.value = false
  }
}

const eliminarItem = async (item) => {
  const id = item.id_item ?? item.id
  if (!confirm('¬øEliminar esta solicitud?')) return
  try {
    await axios.delete(`${API}/${id}`) // DELETE /:id 
    items.value = items.value.filter((x) => (x.id_item ?? x.id) !== id)
  } catch (e) {
    console.error(e)
    error.value = 'No se pudo eliminar el √≠tem'
  }
}

const filtrados = computed(() =>
  items.value
    .filter((i) => i.ubicacion === ubicacionActiva.value)
    .sort((a, b) => (a.estado === b.estado ? 0 : a.estado === 'Pendiente' ? -1 : 1))
)

const conteos = computed(() => {
  const c = { Cocina: 0, Barra: 0 }
  for (const it of items.value) if (c[it.ubicacion] !== undefined) c[it.ubicacion]++
  return c
})

const setUbicacion = (u) => {
  ubicacionActiva.value = u
  nuevoItem.value.ubicacion = u
}

onMounted(obtenerItems)
</script>

<style scoped>
:root { color-scheme: dark light; }
.inv { max-width: 860px; margin: 24px auto; padding: 16px; }
.inv__header { display:flex; align-items:end; justify-content:space-between; gap:12px; margin-bottom:16px; }
h2 { margin: 0; font-size: clamp(1.2rem, 2.5vw, 1.6rem); }

.tabs { display:flex; gap:8px; flex-wrap: wrap; }
.tab { border: 1px solid var(--b, #dcdcdc); background: var(--bg, #fff); padding: 8px 12px; border-radius: 999px; cursor: pointer; }
.tab--active { background: #111; color: #fff; border-color: #111; }
.tab__count { margin-left: 8px; font-weight: 700; padding: 2px 8px; border-radius: 999px; background: rgba(255,255,255,.2); border: 1px solid currentColor; }

.card { background: var(--bg, #fff); border: 1px solid var(--b, #e9e9e9); border-radius: 16px; padding: 16px; margin-bottom: 16px; box-shadow: 0 1px 3px rgba(0,0,0,.05); }
.card__title { margin: 0 0 8px; font-size: 1.1rem; }
.card__title-row { display:flex; justify-content:space-between; align-items:center; margin-bottom: 8px; }

.form { display: grid; gap: 8px; }
.form__row { display:grid; grid-template-columns: 1fr auto; gap:8px; }
.input { border: 1px solid var(--b, #e0e0e0); border-radius: 10px; padding: 10px 12px; background: #fff; }
.input--inline { padding: 6px 10px; }

.btn { border: none; border-radius: 10px; padding: 10px 14px; font-weight: 600; cursor: pointer; background: #111; color: #fff; min-width: 110px; }
.btn--ghost { background: transparent; color: #111; border: 1px solid #111; }
.btn:disabled { opacity: .6; cursor: not-allowed; }

.list { list-style: none; margin: 0; padding: 0; display: grid; gap: 8px; }
.list__item { display:flex; align-items:center; justify-content:space-between; gap: 12px; border: 1px solid var(--b, #eee); border-radius: 12px; padding: 10px 12px; }
.list__main { display:flex; align-items:center; gap:10px; min-width: 0; }
.list__desc { font-weight: 600; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; max-width: 50vw; }
.actions { display:flex; gap: 8px; }
.icon { background: transparent; border: 1px solid var(--b, #ddd); border-radius: 8px; padding: 6px 10px; cursor: pointer; }
.icon--danger { border-color: #d33; color: #d33; }

.chip { border-radius: 999px; padding: 4px 10px; font-size: .85rem; border: 1px solid currentColor; }
.chip--ok { color: #0c8a0c; background: #e9f8e9; }
.chip--warn { color: #a97700; background: #fff6db; }

.empty { display:flex; flex-direction: column; align-items:center; justify-content:center; gap: 8px; padding: 20px; color: #666; text-align: center; }
.spinner { width: 16px; height: 16px; border-radius: 50%; border: 2px solid currentColor; border-right-color: transparent; display:inline-block; animation: spin 0.7s linear infinite; vertical-align: middle; }
@keyframes spin { to { transform: rotate(360deg) } }

@media (max-width: 720px) {
  .form__row { grid-template-columns: 1fr; }
  .list__desc { max-width: 100%; }
}
</style>
